import { Spin } from "antd";
import { useNavigate } from "react-router-dom";
import KeepAlive from "react-activation";

// import ClassificationCard from "@/components/ClassificationCard";
import {
  // CLASSIFYS,
  tplFileNumber,
} from "@/constants";
import useImgsLoaded from "@/hooks/useImgsLoaded";

import Header from "../header";
import { useEffect, useState } from "react";

type TplItem = {
  img: string;
  name: string;
};

export default function Tpls() {
  const navigate = useNavigate();
  const { loading } = useImgsLoaded({ delay: 100 });

  const [tplArr, setTplArr] = useState<TplItem[]>(
    new Array(tplFileNumber).fill(1)
  );

  useEffect(() => {
    async function wait() {
      let res = [];
      for (let i = 0; i < tplArr.length; i++) {
        const data = await import(`@/assets/tpls/tpl${i + 1}.jpeg`);

        res.push({ img: data.default, name: `tpl${i + 1}` });
      }
      return res;
    }
    wait().then((res) => {
      setTplArr(res);
    });
  }, [tplArr.length]);

  const handleToEditor = (name: number | string) => {
    navigate(`/editor?${name}`);
  };
  return (
    <KeepAlive cacheKey="cache_tpls">
      <Header />
      <Spin spinning={loading}>
        <div className="bg-blue-100 py-4">
          {/* <ClassificationCard data={CLASSIFYS} /> */}
          <div className="flex justify-between flex-wrap">
            {tplArr.map((item, i) => (
              <div key={i} className="w-72 h-96 inline-block m-8 relative">
                <div className="w-full h-full absolute bg-gray-500 opacity-0 hover:opacity-80">
                  <div
                    className="m-auto mt-40 w-32  h-10 bg-yellow-500 text-center text-white leading-10 text-2xl font-medium rounded-md cursor-pointer"
                    onClick={() => handleToEditor(item.name)}
                  >
                    使用此模板
                  </div>
                </div>
                <img className="w-full h-full" src={item.img} alt="" />
              </div>
            ))}
            <i className="w-72  m-8"></i>
            <i className="w-72  m-8"></i>
            <i className="w-72  m-8"></i>
            <i className="w-72  m-8"></i>
            <i className="w-72  m-8"></i>
          </div>
        </div>
      </Spin>
    </KeepAlive>
  );
}
